const INC_TO_CM = 2.54;
const INC_TO_PT = 72;
export const PT_TO_PIX = 4/3;

const cm2pix = value => value / INC_TO_CM * INC_TO_PT * PT_TO_PIX;
const inc2pix = value => value * INC_TO_PT * PT_TO_PIX;
const pix2cm = value => value / PT_TO_PIX / INC_TO_PT * INC_TO_CM;
const pix2mm = value => pix2cm(value) * 10;
const pix2inc = value => value / PT_TO_PIX / INC_TO_PT;

export default { cm2pix, inc2pix, pix2cm, pix2mm, pix2inc };